@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Space Mono', monospace;
}

body {
  /* color: rgb(235, 81, 96); */
  line-height: 1.7;
  font-size: 1em;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(/static/media/nasa-Q1p7bh3SHj8-unsplash.ae8ebf5a.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* Nasa Picture */

.nasa-photo {
  display: flex;
  padding: 40px;
  margin: 0 auto;
  max-height: 100%;
  background-color: rgba(223,224,226,.3);
  color: rgb(235, 81, 96);
  font-weight: 700;
}

.nasa-photo img {
  width: 50%;
  margin-right: 50px;
  object-fit: contain;
  max-height: 80vh;
  flex: 1 1;
}

.nasa-photo iframe {
  margin-right: 50px;
  flex: 1 1;
}

.nasa-photo .content {
  flex: 2 1;
}

.nasa-photo h1 {
  margin-top: 0;
  margin-bottom: 30px;
  font-weight: 700;
}

.nasa-photo .date {
  margin-top: 0;
  margin-bottom: 10px;
  color: rgb(183, 153, 200);
}

.nasa-photo .copyright {
  color: rgb(183, 153, 200);
}

.nasa-photo .explanation {
  color: black;
}

/* Favorites  */

#favorite {
  background-color: rgba(223,224,226,.6);
}

/* Home page */

.welcome {
  margin-top: 4em;
  color: rgba(223,224,226,1);
}


